import Pizzicato from 'pizzicato';
import jazzMp3 from './jazz-ambient.mp3';

const audio = new Pizzicato.Sound({
  source: 'file',
  options: {
    path: jazzMp3,
    volume: 0.2,
    attack: 5,
    release: 5,
    loop: true
  }
}, () => {
  audio.stop();
});

export default audio;
